import { Helmet as Head } from "react-helmet";
export default function Helmet() {
	return (
		<Head>
			<title>{process.env.REACT_APP_META_TITLE}</title>
			<meta
				name="description"
				content={process.env.REACT_APP_META_DESCRIPTION}
			/>
			<meta name="twitter:card" content="summary_large_image" />
			<meta
				name="twitter:site"
				content={process.env.REACT_APP_META_TWITTER_SITE}
			/>
			<meta
				name="twitter:creator"
				content={process.env.REACT_APP_META_TWITTER_CREATOR}
			/>
			<meta
				name="twitter:title"
				content={process.env.REACT_APP_META_TITLE}
			/>
			<meta
				name="twitter:description"
				content={process.env.REACT_APP_META_DESCRIPTION}
			/>
			<meta
				name="twitter:image"
				content={process.env.REACT_APP_META_TWITTER_IMAGE}
			/>
			<meta
				property="og:title"
				content={process.env.REACT_APP_META_TITLE}
			/>
			<meta
				property="og:description"
				content={process.env.REACT_APP_META_DESCRIPTION}
			/>
			<meta property="og:image" content="url_to_image" />
			<meta
				property="og:url"
				content={process.env.REACT_APP_META_FACEBOOK_IMAGE}
			/>
			<meta property="og:site_name" content={process.env.REACT_APP_META_FACEBOOK_SITE_NAME} />
			<meta property="og:locale" content={process.env.REACT_APP_META_FACEBOOK_LOCALE} />
			<meta property="og:type" content="article" />
			<meta property="fb:app_id" content={process.env.REACT_APP_META_FACEBOOK_APP_ID} />
		</Head>
	);
}
