import Timer from "../Components/Timer";
import OptIn from "../Components/OptIn";
import Preloader from "../Components/Preloader";
import Helmet from "../Components/Helmet";
import "./styles.css";

function App() {
	return (
		<div className="App">
			<Helmet />
			<div className="container">
				<h1>
					{process.env.REACT_APP_TITLE}
					<br />
					<br />
					Coming Soon
				</h1>
				<Preloader />
				<Timer />
				<OptIn />
			</div>
		</div>
	);
}

export default App;
