import MailchimpSubscribe from "react-mailchimp-subscribe";
import { useState } from "react";
function validateEmail(email) {
	var re = /\S+@\S+\.\S+/;
	return re.test(email);
}

// a basic form
const CustomForm = ({ status, message, onValidated }) => {
	const [valid, setIsValid] = useState(true);
	const [input, setInput] = useState({});
	const validEmail = validateEmail(input?.EMAIL || "");

	const submit = async () => {
		if (!input.EMAIL || !validEmail || !input.FNAME || !input.LNAME) {
			return setIsValid(false);
		}

		setIsValid(true);
		onValidated(input);
	};

	const handleSetInput = e => {
		if (!e) return;
		const k = e.target.name;
		const v = e.target.value;
		setInput(input => {
			return { ...input, [k]: v };
		});
	};

	const invalid = ({ show, isValid }) => {
		if (!show) return;
		if (!isValid)
			return {
				style: {
					border: "1px solid red",
				},
			};
	};

	return (
		<div
			style={{
				borderRadius: 2,
				padding: 10,
				display: "inline-block",
			}}
		>
			{!valid && (
				<div style={{ color: "blue" }}>
					Please fill out all required inputs
				</div>
			)}
			{status === "sending" && (
				<div style={{ color: "blue" }}>sending...</div>
			)}
			{status === "error" && (
				<div
					style={{ color: "red" }}
					dangerouslySetInnerHTML={{ __html: message }}
				/>
			)}
			{status === "success" && (
				<div
					style={{ color: "green" }}
					dangerouslySetInnerHTML={{ __html: message }}
				/>
			)}
			<br />

			{/* Email */}
			<label>Email Address</label>
			<br />
			<input
				onChange={handleSetInput}
				name="EMAIL"
				type="email"
				{...invalid({
					show: !valid,
					isValid: validEmail,
				})}
			/>
			<br />

			{/* First Name */}
			<label>First Name</label>
			<br />
			<input
				onChange={handleSetInput}
				name="FNAME"
				type="text"
				{...invalid({
					show: !valid,
					isValid: input.FNAME,
				})}
			/>
			<br />

			{/* Last Name */}
			<label>Last Name</label>
			<br />
			<input
				onChange={handleSetInput}
				name="LNAME"
				type="text"
				{...invalid({
					show: !valid,
					isValid: input.LNAME,
				})}
			/>
			<br />

			{/* Business Name */}
			<label>Business Name</label>
			<br />
			<input onChange={handleSetInput} name="BUSINESS" type="text" />
			<br />

			{/* Website */}
			<label>Website</label>
			<br />
			<input onChange={handleSetInput} name="WEBSITE" type="text" />
			<br />

			{/* Phone Number */}
			<label>Phone Number</label>
			<br />
			<input onChange={handleSetInput} name="PHONE" type="tel" />
			<br />

			<span className="span-btn" onClick={submit}>
				Submit
			</span>
		</div>
	);
};

export default function MailchimpForm({ onSubmit }) {
	return (
		<>
			<h2>Bingo!Bango!Botto!</h2>
			<p>
				Subscribe to our mailing list to be notified when the site is
				live!
			</p>

			<MailchimpSubscribe
				url={process.env.REACT_APP_MAILCHIMP_SUBSCRIBE_URL}
				render={({ subscribe, status, message }) => (
					<CustomForm
						status={status}
						message={message}
						onValidated={subscribe}
					/>
				)}
			/>
		</>
	);
}
